import {play, useComponents} from 'vue-play'
import 'vue-play/dist/vue-play.css'

// load example components
import basicExample from './examples/basic.vue'
import clickOutsideExample from './examples/click-outside.vue'
import centeredExample from './examples/centered.vue'

// regisrer the component to play
import SlimModal from '../src'
useComponents({
  SlimModal
})
console.log(basicExample);
// play components
play({
  SlimModal: {
    'Basic': basicExample,
    'Close on click outside': clickOutsideExample,
    'Centered': centeredExample
  }
})



